import React, { Component } from "react"
import { Index } from "elasticlunr"
import { Link } from "gatsby"

export default class Search extends Component {
    constructor(props) {
        super(props)
        this.state = {
            query: ``,
            results: []
        }
    }

    render() {
        return (
            <div className="searchBox">
            <form action="">
                <input type="search" value={this.state.query} onChange={this.search} placeholder="Buscar" />
                <i class="fa fa-search"></i>
                <ul>
                    {this.state.results.map(page => (
                        <li key={page.id}>
                            <Link to={page.path}>{page.title}</Link>
                            { ` (` + page.tags.join(`, `) + `)`} { page.excerpt }

                        </li>
                        ))}
                </ul>
            </form>
            </div>
        )
    }
    getOnCreateIndex = () => this.Index ? this.index : Index.load(this.props.searchIndex)

    search = evt => {
        const query = evt.target.value
        this.index = this.getOnCreateIndex()
        this.setState({
            query, 
            results: query.length > 3 ? (this.index.search(query, { expand: true }).map(({ ref }) => this.index.documentStore.getDoc(ref))) : []
        })        
    }
}