import React from 'react';
import { slide as BurgerMenu } from 'react-burger-menu';
import { Link, StaticQuery, graphql } from 'gatsby'

import Bio from './bio'
import { kebabCase } from 'lodash';

class Menu extends React.Component {
    render() {
        const siteMetadata = this.props.siteMetadata;
        return (
            <StaticQuery query={graphql`
            query {
              allMarkdownRemark(limit: 2000) {
                group(field: frontmatter___tags) {
                  fieldValue
                  totalCount
                }
              }
            }
          `}
                render={data => (
                    <BurgerMenu>
                        <div className="burger-content">
                        <Bio siteMetadata={siteMetadata} showContent={true} />
                        <div className="contact">
                            <h3 className="contact-title">Tags</h3>
                            <ul className="post-tags">
                                {data.allMarkdownRemark.group.map(tag => (
                                    <li key={tag.fieldValue}>
                                        <Link to={`/tags/${kebabCase(tag.fieldValue)}/`} className="tag">#{tag.fieldValue}</Link>
                                    </li>
                                ))}
                            </ul>
                        </div>
                        <footer>
                            <div className="copyright">
                                <p>{new Date().getFullYear()} &copy; {siteMetadata.author}</p>
                            </div>
                        </footer>
                        </div>
                    </BurgerMenu>
                )}
            />
        )
    }
}

export default Menu